import React from 'react'
import {Link} from 'react-router-dom'
export default function CartTotals({value}) {
    const {total, tax, finalPrice, clearCart} = value
    return (
        <React.Fragment>
            <div className="container">
                <div className="row">
                    <div className="col-10 col-sm-8 mt-2 ml-md-5 ml=md-auto text-capitalize text-right">
                            <button
                                className="btn btn-outline-danger text-uppercase mb-3 px-5"
                                onClick={()=>clearCart()}>
                                Clear Cart
                            </button>
                        <h5>
                            <span className="text-title">
                                total is :$
                                <strong>{total}</strong>
                            </span>
                        </h5>
                        <h5>
                            <span className="text-title">
                                tax is:$ 
                                <strong>{tax}</strong>
                            </span>
                        </h5>
                        <h5>
                            <span className="text-title">
                                final Price is :$
                                <strong>{finalPrice}</strong>
                            </span>
                        </h5>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}



