import React, { Component } from 'react'
import Title from '../Title'
import CartCoulmn from './CartColumn'
import EmptyCart from './EmptyCart'
import Cartlist from './Cartlist'
import {Proconsumer} from '../../context'
import CartTotals from './CartTotals'
export default class Cart extends Component {
    render() {
        return (
            <section>
            <Proconsumer>
                {v=>{
                  const {cart} = v 
                  if(cart.length > 0) {
                      return (
                        <React.Fragment>
                            <Title name="your" title="cart"/>
                            <CartCoulmn/>
                            <Cartlist value={v} cart={cart}/>
                            <CartTotals value={v}/>
                        </React.Fragment>
                      )
                  }else{
                    return (
                      <React.Fragment>
                          <EmptyCart/>
                          <CartTotals value={v}/>
                      </React.Fragment>
                    )
                    
                  }
                }}
            </Proconsumer>
               
            </section>
        )
    }
}


