import React, { Component } from 'react'
import {Proconsumer} from '../context'
import {Link} from 'react-router-dom'
import {ButtonStyle} from './style'

export default class Details extends Component {
    render() {
        return (
            <Proconsumer>
               {v=>{
                   const {id, title, img, price, company,  info, inCart} = v.details
                   return (
                       <div className="container my-5">
                           <div className="row">
                               <div className="col-10 mx-auto my-5 text-center text-slanted text-blue">
                                    <h1>{title}</h1>
                               </div>
                           </div>
                           <div className="row">
                               <div className="col-10 col-md-6 mx-auto my-3 text-capitalize">
                                    <img src={img} alt="product" className="img-fluid"/>
                               </div>
                               <div className="col-10 col-md-6 mx-auto my-3 text-capitalize">
                                    <h2>
                                        model : {title}
                                    </h2>
                                    <h4 className="text-title text-uppercase text-muted mt-3 mb-2">
                                        made by : <span className="text-uppercase">{company}</span>
                                    </h4>
                                    <h4 className="text-blue">
                                       <strong>
                                         price : <span>$</span>
                                        {price}
                                       </strong>
                                    </h4>
                                    <p className="text-capitalize font-weight-bold mt-3 mp-0">
                                        product information : 
                                    </p>
                                    <p className="text-muted lead">
                                       {info} 
                                    </p>
                                    <div>
                                        <Link to="/">
                                            <ButtonStyle>
                                                back to products
                                            </ButtonStyle>
                                        </Link>
                                        <ButtonStyle
                                            cart
                                            disabled={inCart}
                                            onClick={()=>{
                                                v.addToCarts(id)
                                                v.openModel(id)
                                            }}
                                        >
                                                {inCart?'in Carts':'add to carts'}
                                        </ButtonStyle>
                                    </div>
                               </div>
                           </div>
                       </div>
                   )
               }}
            </Proconsumer>
        )
    }
}



