import React, { Component } from 'react'
import Product from './Product'
import {Proconsumer} from '../context'
import Titel from './Title'
export default class ProductList extends Component {
    stste={
        products:[]
    }
    render() {
        return (
            <React.Fragment>
                <div className="py-5">
                    <div className="container">
                        <Titel name="our" title="products"/>
                        <div className="row">
                            <Proconsumer>
                                {(v)=>{
                                    return v.products.map(pro=>{
                                        return <Product key={pro.id} pro={pro}/>
                                    }) 
                                }}
                            </Proconsumer>
                        </div>
                    </div>
                </div>
            </React.Fragment>
                
        )
    }
}



