import React from 'react'

export default function Cartitem({value, item}) {
    const {id, title, img, price, total, count} = item
    const {increment, decrement, remove } = value
    return (
        <div className="row my-2 text-capitalize text-center">
            <div className="col-10 col-lg-2 mx-auto">
                <img src={img} alt="item"
                     style={{height:'5rem', width:'5rem'}}
                     className="img-fluid"
                    />
            </div>
            <div className="col-10 col-lg-2 mx-auto">
                <span className="d-lg-none">product : </span>
                {title}
            </div>
            <div className="col-10 col-lg-2 mx-auto">
            <span className="d-lg-none">price : </span>
                {price}
            </div>
            <div className="col-10 col-lg-2 mx-auto my-2 my-lg-0">
               <div className="d-flex justify-content-center">
                  <span className="btn btn-black mx-1"
                        onClick={()=>{decrement(id)}}>-</span>
                  <span className="btn btn-black mx-1">{count}</span>
                  <span className="btn btn-black mx-1"
                        onClick={()=>{increment(id)}}>+</span>
               </div>
            </div>
            <div className="col-10 col-lg-2 mx-auto">
               <div className="cart-icon" onClick={()=>{remove(id)}}>
                    <i className="fas fa-trash"/>
               </div>
            </div>
            <div className="col-10 col-lg-2 mx-auto">
                <strong>item total :$ </strong>
                {total}
            </div>
        </div>
    )
}



