import React, { Component } from 'react'
import {storeProducts, detailProduct} from './data'
const con = React.createContext();
class Proprovider extends Component {
    state = {
        products : [],
        details  : [],
        cart :[],
        modelOpend:false,
        modelProduct:[],
        total:0,
        tax:0,
        finalPrice:0
    } 
    getItem = (id)=>{
        const product = this.state.products.find(item => item.id === id)
            return product
    }
    handelDetails = (id)=>{
        const product = this.getItem(id)
        this.setState(()=>{
            return {
                details:product
            }
        })
    }
    addToCarts = (id)=>{
        let tempProducts = [...this.state.products]
        const index = tempProducts.indexOf(this.getItem(id))
        const product = tempProducts[index]
        product.inCart = true
        product.count = 1
        const price = product.price
        product.total = price
        this.setState(
           ()=>{
               return {
                   products:tempProducts,
                   cart:[...this.state.cart, product]
               }
           }
       ,()=>{
            this.addTotals()
       }
    )
    }
    openModel= id=>{
        const product = this.getItem(id);
        this.setState(()=>{
            return {
                modelProduct:product,
                modelOpend:true
            }
        },()=>{
            console.log(this.state.modelProduct)
        }) 
    }
    colseModel = ()=>{
        this.setState(()=>{
            return {
                modelOpend:false
            }
        })
    }
    increment = (id) =>{
        let tempCart = [...this.state.cart]
        const selectedProduct = tempCart.find(item=>item.id===id);
        const index = tempCart.indexOf(selectedProduct)
        const product = tempCart[index]
        product.count = product.count+1
        product.total = product.price * product.count
        this.setState(()=>{
            return {
                cart : [...tempCart]
            }
        },()=>{
            this.addTotals()
        })
    }
    decrement = (id) =>{
        let tempCart = [...this.state.cart]
        const selectedProduct = tempCart.find(item=>item.id===id);
        const index = tempCart.indexOf(selectedProduct)
        const product = tempCart[index]
        product.count = product.count-1
        product.total = product.price * product.count
        if (product.count > 0){
            this.setState(()=>{
                return {
                    cart : [...tempCart]
                }
            },()=>{
                this.addTotals()
            })
        }else{
            this.remove(id)
        }
        
    }
    remove = (id) =>{
        let tempProducts = [...this.state.products]
        let tempCart = [...this.state.cart]
        tempCart  = tempCart.filter(item=>item.id!==id)
        const removedIndex = tempProducts.indexOf(this.getItem(id))
        let removedItem = tempProducts[removedIndex]
        removedItem.count=0
        removedItem.inCart=false
        removedItem.total=0
        this.setState(()=>{
            return {
                products : [...tempProducts],
                cart : [...tempCart]
            }
        },()=>{
            this. addTotals()
        })

    }
    clearCart = () =>{
        this.setState(()=>{
            return {cart:[]}
        },()=>{
            this.setproducts()
            this.addTotals()
        })
    }
    addTotals = ()=>{
        let subTotal = 0;
        this.state.cart.map(item=>{
            subTotal += item.total
        })
        const taxTemp = subTotal * 0.1;
        const tax = parseFloat(taxTemp.toFixed(2))
        const total = subTotal + tax
        this.setState({
            total:subTotal,
            tax:tax,
            finalPrice:total
        })
    }
    componentDidMount(){
        this.setproducts()
        // this.refTest()
        
    }
    // refTest = ()=>{
    //     console.log('state : ',this.state.products[0].inCart)
    //     console.log('store : ',storeProducts[0].inCart)
    //     const test = [...this.state.products]
    //     test[0].inCart=true
    //     this.setState(()=>{
    //         return {
    //             products:test
    //         }
    //     },()=>{
    //         console.log('state : ',this.state.products[0].inCart)
    //         console.log('store : ',storeProducts[0].inCart)
    //     })
    // }
    setproducts = ()=>{
        let tempProduct = []
        storeProducts.forEach(item=>{
            const setitem = {...item}
            tempProduct = [...tempProduct,setitem]
        })
        // console.log('state : ',this.state.products[0].inCart)
        // console.log('store : ',storeProducts[0].inCart)
        // tempProduct[0].inCart = true      
        this.setState(()=>{
           return {
               products:tempProduct
            }
        },()=>{
            // console.log('state : ',this.state.products[0].inCart)
            // console.log('store : ',storeProducts[0].inCart)
        })
    }
    
    render() {
        return (
            <con.Provider value={{
                ...this.state,
                handelDetails:this.handelDetails,
                addToCarts:this.addToCarts,
                openModel:this.openModel,
                colseModel:this.colseModel,
                increment:this.increment,
                decrement:this.decrement,
                remove:this.remove,
                clearCart:this.clearCart
            }}>
                {this.props.children}
            </con.Provider>
        )
    }
}
const Proconsumer = con.Consumer;
export {Proprovider, Proconsumer}


