import React from 'react'
import Cartitem from './Cartitem'
export default function Cartlist({value, cart}) {
    return (
        <div className="container-fluid">
            {cart.map(item=>{
                return <Cartitem key={item.id} value={value} item={item}/>
            })}  
        </div>
    )
}



