import React, { Component } from 'react'
import {Proconsumer} from '../context'
import styled from 'styled-components'
import {ButtonStyle} from './style'
import {Link} from 'react-router-dom'

export default class AddItemModel extends Component {
    render() {
        return (
            <Proconsumer>
                {v=>{
                    const {modelOpend,colseModel} = v
                    const {img, title, price} = v.modelProduct
                    if(!modelOpend){
                        return null
                    }else{
                        return (
                            <StyledModel>
                                <div className="container">
                                    <div className="row">
                                        <div id="model" className="col-8 col-md-6 col-lg-4 mx-auto py-3 text-center text-capitalize">
                                            <h5>the item added to cart</h5>
                                            <img src={img} alt="pro" className="img-fluid"/>
                                            <h3>{title}</h3>
                                            <h5 className="text-muted">price is : {price}</h5>
                                            <Link to="/">
                                                <ButtonStyle onClick={()=>colseModel()}>
                                                    continue shopping
                                                </ButtonStyle>
                                            </Link>
                                            <Link to="/cart">
                                                <ButtonStyle
                                                     cart
                                                     onClick={()=>colseModel()}>
                                                     go to cart
                                                </ButtonStyle>
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </StyledModel>
                        )
                    }
                }}
            </Proconsumer>
        )
    }
}
const StyledModel = styled.div`
    position:fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:rgba(0,0,0,0.2);
    display:flex;
    align-items: center;
    justify-content:center;
    #model{
        border-radius:20px;
        background:var(--mainWhite)
    }
`


